html,
body {
  background-color: black;
  color: #e5af37;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}
.App {
  text-align: center;
  height: 100%;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}
.date {
  color: #e5af37;
}
.clac-table {
  border: 4px solid #c4ae78;
  padding: 5px;
  color: #e5af37;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
  font-size: 26px;
}
.calc-22 {
  margin-top: 25px;
}

th,
td {
  border: 4px solid #c4ae78;
  padding: 5px;
  color: #e5af37;
}
tr {
  margin: 50px;
}
input {
  border: 4px solid #c4ae78;
  padding: 5px;
  color: black;
  font-size: 24px;
  width: 150px;
  display: block;

  margin-block: 15px;
}

.price-table {
  margin-left: auto;
  margin-right: auto;
  font-size: 26px;
}

.alora-logo,
.bar-brand {
  width: 25%;
  height: 25%;
  margin-top: 2%;
  overflow: hidden;
  border-radius: 50%;
}
.whatsapp {
  color: green;
  background-color: white;
  font-size: 24px;
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
  display: block;
  text-align: center;
}
.sell-price {
  color: green;
}
.buy-price {
  color: red;
}
.table-frame {
  padding: 5px;
  color: #e5af37;
  margin-left: auto;
  margin-right: auto;
  border: none;
}
.alora-terms {
  text-align: right;
}
.price-up {
  color: white;
  background-color: green;
  font-size: 22px;
}
.price-down {
  color: white;
  background-color: red;
  font-size: 22px;
}
@media (min-width: 320px) and (max-width: 480px) {
  body {
    font-size: 12px;
  }
  .alora-logo {
    width: 50%;
    height: 50%;
    margin-top: 2%;
    overflow: hidden;
    border-radius: 50%;
  }
  .row-zakat {
    width: 10%;
  }
}
